import { Component} from '@angular/core';
import { GetreqService } from '../../service/getreq.service';


@Component({
  selector: 'app-reviews',
  templateUrl: './reviews.component.html',
  styleUrls: ['./reviews.component.css']
})
export class ReviewsComponent {
 
  teams: any;
  

  constructor(private getReq:GetreqService) {

    this.getReq.getTasks().subscribe(teams =>{
    this.teams=teams
    })
  }


  }



