import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NotificationsService } from 'angular2-notifications';
import { AuthService } from '../auth.service';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  username:string;
  password:string;
  constructor(private authservice:AuthService, private _notification: NotificationsService, private router:Router) { }

  ngOnInit() {
  }
  onLoginSubmit(){
    const user ={
      username:this.username,
      password:this.password,
      
    }
    this.authservice.authenticateUser(user).subscribe(data=>{
      if(data.success){
        this.authservice.storeUserData(data.token, data.user);
        this._notification.success('You are now logged in')
         this.router.navigate(['forms'])
      }else{
        this._notification.error(data.msg)
          this.router.navigate(['dashlogin'])
      }
    })
   }

}



