import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { GetreqService } from '../../service/getreq.service';

@Component({
  selector: 'app-allreviews',
  templateUrl: './allreviews.component.html',
  styleUrls: ['./allreviews.component.css']
})
export class AllreviewsComponent implements OnInit {
  
  cricket:any;
  showspin:boolean=true;
  viewMode = 'overview'
  
 
  //  private sub: any;
  constructor(private route: ActivatedRoute, private getreq:GetreqService) { 
 
  }

  ngOnInit() {
    window.scrollTo(0, 0)
    //  this.sub = this.route.params.subscribe(params => {
    //   this.cric = params['cric']; // (+) converts string 'id' to a number
    
      // In a real app: dispatch action to load the details here.
  //  });
  this.cricket = this.route.snapshot.paramMap.get(('cricket'));
  // alert(this.cricket);
  this.getreq.GetSingleTask(this.cricket)
  .subscribe(cricket => {
      this.cricket = cricket;
      this.showspin=false;
  });
  
  }
  
  // ngOnDestroy() {
  //   this.sub.unsubscribe();
  // }

}



