import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NotificationsService } from 'angular2-notifications';
import { ValidateService } from '../../dashboard/validate.service';
import { AuthService } from '../../dashboard/auth.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
  user:Object;
  constructor(private authservice:AuthService, private validatesvs:ValidateService, private router:Router, private _notification: NotificationsService) { }

  ngOnInit() {
    this.authservice.getProfile().subscribe(profile =>{
      this.user = profile.user; 
    },
    err =>{
    console.log(err);
    return false;
    });
  }
  onLogout(){
    this.authservice.logout();
    this._notification.success('You are logged out');
    this.router.navigate(['/dashlogin']);
    return false;
  }
}



