import { Component, OnInit } from '@angular/core';

import { NotificationsService } from 'angular2-notifications';
import { GetPostService } from '../getpost.service';

@Component({
  selector: 'app-forms',
  templateUrl: './forms.component.html',
  styleUrls: ['./forms.component.css']
})
export class FormsComponent implements OnInit {
  cricket:any
  imgurl:string;
  from_team: string;
  to_team:string;
 
  to_imgurl:string;
  from_imgurl:string;
  match_desc:string;
  team_one_11:string;
  team_two_11:string;
  dream11:string;

  date_match = new Date().toDateString();
  constructor(private getreq:GetPostService,  private _notification: NotificationsService) { 
 
    this.getreq.getTasks()
    .subscribe(cricket => {
        this.cricket = cricket;
    });
  }

  ngOnInit() {
  }
  addTask(event){
    event.preventDefault();
    const newTask = {
      imgurl: this.imgurl,
      from_team:this.from_team,
      to_team:this.to_team,
      date_match:this.date_match,
      to_imgurl:this.to_imgurl,
      from_imgurl:this.from_imgurl,
      match_desc:this.match_desc,
      team_one_11:this.team_one_11,
      team_two_11:this.team_two_11,
      dream11:this.dream11
   }
   this.getreq.addTask(newTask)
   .subscribe(task => {
       this.cricket.push(task);
       alert("posted")
       this._notification.success('Success posted')
   });
  }

}



