import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';


import { AppComponent } from './app.component';
import { NavbarComponent } from './home/navbar/navbar.component';
import { SliderComponent } from './home/slider/slider.component';
import { GridComponent } from './home/grid/grid.component';
import { FooterComponent } from './home/footer/footer.component';
import { FirstComponent } from './review/first/first.component';
import { ChennaiComponent } from './teams/chennai/chennai.component';
import { RouterModule } from '@angular/router';
import { HomeComponent } from './home/home/home.component';
import { TeamService } from './service/team.service';
import { MumbaiComponent } from './teams/mumbai/mumbai.component';
import { GetreqService } from './service/getreq.service';
import { ReviewsComponent } from './home/reviews/reviews.component';

import { AllreviewsComponent } from './review/allreviews/allreviews.component';
import { Home2Component } from './home2/home2.component';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { SearchFilterPipe } from './SearchFilterPipe';
import { LoginComponent } from './dashboard/login/login.component';
import { FormsComponent } from './dashboard/forms/forms.component';
import { GetPostService } from './dashboard/getpost.service';
import { AuthguardService } from './dashboard/authguard.service';
import { AuthService } from './dashboard/auth.service';
import { ValidateService } from './dashboard/validate.service';
import { SimpleNotificationsModule } from 'angular2-notifications';
import { FormComponent } from './separate/form/form.component';
import { SpinnerComponent } from './spinner/spinner.component';
import { CricslidesComponent } from './cricslides/cricslides.component';


const routes =([{path:'', component:Home2Component},{path:'home', component:HomeComponent},{path:'chennai', component:ChennaiComponent},
{path:"mumbai", component:MumbaiComponent},{path:"reviews", component:ReviewsComponent},
{path:'task/:cricket', component:AllreviewsComponent},
{path:'dashlogin', component:LoginComponent},{path:'forms', component:FormsComponent, canActivate:[AuthguardService]}])
@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    SliderComponent,
    GridComponent,
    FooterComponent,
    FirstComponent,
    ChennaiComponent,
    HomeComponent,
    MumbaiComponent,
    ReviewsComponent,
    AllreviewsComponent,
    Home2Component,
    SearchFilterPipe,
    LoginComponent,
    FormsComponent,
    FormComponent,
    SearchFilterPipe,
    SpinnerComponent,
    CricslidesComponent
    
   
    
  
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
     RouterModule.forRoot(routes),
     NgbModule.forRoot(),
     SimpleNotificationsModule
  ],
  providers: [TeamService, GetreqService, GetPostService,AuthguardService, AuthService, ValidateService],
  bootstrap: [AppComponent]
})
export class AppModule { }



