import { Component } from '@angular/core';
import { GetreqService } from '../../service/getreq.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.css']
})
export class FormComponent {
  cricket:any
  imgurl:string;
  from_team: string;
  to_team:string;
  date_match:string;
  to_imgurl:string;
  from_imgurl:string;
  match_desc:string;
  showspinner:boolean = true;
  
  constructor(private getreq:GetreqService, private router:Router ) {
    this.getreq.getTasks()
    .subscribe(cricket => {
        this.cricket = cricket;
        this.showspinner=false;
    });
   }
  //  goToProductDetails(cricket) {
  //   this.router.navigate(['/task', {cricket:cricket}]);
  // }
  addTask(event){
    event.preventDefault();
    const newTask = {
      imgurl: this.imgurl,
      from_team:this.from_team,
      to_team:this.to_team,
      date_match:this.date_match,
      to_imgurl:this.to_imgurl,
      from_imgurl:this.from_imgurl,
      match_desc:this.match_desc
   }
   this.getreq.addTask(newTask)
   .subscribe(task => {
       this.cricket.push(task);
      this.imgurl="",
      this.from_team ="",
       this.to_team ="",
       this.date_match ="",
       this.to_imgurl ="",
       this.from_imgurl ="",
      this.match_desc =""
       this.router.navigate(['/'])
   });
  }
 

}



